.card-post {
  width: auto;
  height: 500px;
  border-radius: 5px;
  /* margin-right: 20px; */
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  flex-direction: column;
  box-shadow: rgba(0, 0, 0, 0.24) 7px 6px 12px;
}
 .thumbnail {
padding:0px;
overflow:hidden;
height:150%

} 

.content{
	padding: 20px;
}
.data{
  color:#b02f00;
}
span{
  font-weight:bold;
  font-size:16px;
  font:-webkit-control;
  color:#b02f00;
 float:left;
  right:0px;
}
.like{
  position:relative;
  border-radius:50%;
    border:1px solid black;
    width: 30px;
    height: 30px;
 float:right;
    cursor:pointer;
}
.click{
  position:relative;
  top:-2px;
  right:5px;
  opacity:0;
  width:20px;
  height:35px;
  border-radius:50%;
}
.count{
  position:relative;
top:28px;
right:-55px;
  margin:5px;
  font-weight:bold !important;
  float:right;
}
img{
  position:relative;
  width:100%;
}
.event-card{
 
  padding: 20px;
  background-color: white;
  color: var(--Black);
  border-radius: 5px;

  display: flex;
  align-items: flex-start;
  justify-content: end;
  flex-direction: column;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
position:sticky;
width: 80%;
height: 70%;
left:10%;
top: 12%;


}
.row{
  display:flex;
width:100%;
height:100vh;
}
.row div{
  flex:1;
  
}

.popbtn{
  position: relative;
    width: 37%;
    height: 14%;
    left: 3%;
    bottom: -8%;
    font-size: 22px;
}
.popdata{
     padding: 16px;
    padding-left: 22px;
    margin: 2%;
    width: 90%;
    background: #FFFFFF;
    border: 2px solid #6FCF97;
    box-sizing: border-box;
    border-radius: 10px;
    font-size: 20px;
}
.poptitle{
     font-size: 36px;
    margin-bottom: 30px;
    margin-left: 21px;
    margin-top: 15px;
    line-height: 54px;
}

.columnright{
  width:387px;
  height:436px;
  background:grey;
  border-radius:10px;
}
.popimg{
  position:relative;
top:50%;
left:45%;

}
@media (max-width: 480px){
  .poptitle{
    
    font-size: 26px;
    /* margin-bottom: 13px; */
    margin-left: 10px;
    margin-top: -19px;
    line-height: 37px;
    /* justify-content: center; */
    /* align-items: center; */
    /* margin: auto; */
    position: absolute;
    left: 28%;
}
  .popbtn{
  
         width: 160%;
    top: 180px;
    height: 44px;
    justify-content: center;
    left: 7px;
  }
  .row{
 flex-direction:column;
width:auto;
}
.popdata{
     padding: 10px;
    padding-left: 15px;
    margin-top: 15px;
    width: 160%;
    background: #FFFFFF;
    border: 1px solid #6FCF97;
    box-sizing: border-box;
    border-radius: 10px;
    font-size: 16px;
}
.columnleft{

  flex-direction:column;
  height:200px !important;
  
}
.columnright{
 position: absolute;
    top: 212px;
    width: 90%;
    height: 166px;
}
.event-image{
     position: relative !important;
    width: 100% !important;
    height:100% !important;
    top: -109px !important;
  
}
} 




/* add image css */
input[type="file"]{
  display:none;
}
label{
      position: relative;
    color: white;
    text-align: center;
    top: 50%;
    left: 30%;
}
.event-image{
        position: relative;
    height: 450px;
    width: 100%;
    top: -255px;
    left: -30%;
    border-radius: 10px;
}